import * as React from 'react';
import Fab from '@mui/material/Fab';
import Snackbar from '@mui/material/Snackbar';
import DownloadIcon from '@mui/icons-material/Download';
import JSZip from 'jszip'
import { saveAs } from 'file-saver';

export default function FloatingDownloadButton({data}:any) {
  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    const content = JSON.parse(data);
    const onlyCourses = content.courses;
    let filesObject:any = {};
    onlyCourses.forEach((c: any)=>{
      if (!filesObject[c._language]) filesObject[c._language] = [];
      filesObject[c._language].push(c);
    });

    Object.keys(filesObject).forEach((language):any=>{
      if ((language !== 'All' && language !== 'None') && filesObject['All'] ) filesObject[language]=[...filesObject[language], ...filesObject['All']]
    });


    let zip = new JSZip();

    Object.keys(filesObject).forEach(language=>{
      let blob = new Blob([JSON.stringify(filesObject[language])], {
        type: "application/json",
      });
      zip.file(`content/v${content.version}_${language.toLowerCase()}.min.json`, blob);
    })

    zip.generateAsync({
      type: "blob"
  }).then(function(content) {
      // see FileSaver.js
      saveAs(content, "content.zip");
  });

    // navigator.clipboard.writeText(JSON.stringify(filesObject));
    setOpen(true);
  };

  return (
    <div>
    <Fab color="primary" onClick={handleClick} sx={{position: 'fixed', bottom: 96, right: 32, zIndex: 10}}>
  <DownloadIcon />
</Fab>
      <Snackbar
        open={open}
        autoHideDuration={6000}
        message="Download started"
      />
    </div>
  );
}